import wx from 'weixin-jsapi';
import { isPrimitive } from 'util';

export default {
    data () {
        return {
            form: {
                title: '',
                description:'',
                serialID:'',
                priority:'',
                date:''
            },
            Submit: 'Submit',
            isSubmit: false,
            validateRules: [
                { validate: (val) => !!val, message: 'Please input Title' }
            ]
        }
    },
    methods: {
        submitTicket: function() {
            let thiz = this;
            let isPrd =  Object.is(process.env.NODE_ENV, 'production');
            if (!this.form.title) {
                this.$alert()
            } else {
                let loading = this.$loading();
                this.$axios({
                    url: this.CONFIG.url.createTicket,
                    data: {
                        Name: this.form.title,
                        Description: this.form.description,
                        SerialID: this.form.serialID,
                        ServicePriorityCode: this.form.priority,
                        OnSiteArrivalDateTime: this.form.date
                    },
                    method: 'post'
                }).then((res) => {
                    console.log(res);
                    loading.close();
                    if(res.status == 200) {
                        // thiz.Submit = 'Submit';
                        thiz.isSubmit = false;
                        thiz.$toast.success('Create Successfully');
                        if (isPrd) {
                            wx.ready(() => {
                                wx.closeWindow();
                            })
                        }
                    }
                })
            }
        },
        cancelInput: function () {
            this.form = {
                title: '',
                description:'',
                serialID:'',
                priority:'',
                date:''
            }
        }
    },
    mounted: function () {
        this.$axios({
            method: 'post',
            url: this.CONFIG.url.wxConfig,
            data: {
                url: location.href.split('#')[0]
            }
        }).then((data) => {
            console.log(data.data);
            wx.config({
                debug: true,
                appId: data.data.appId,
                timestamp: data.data.timestamp,
                nonceStr: data.data.nonceStr,
                signature: data.data.signature,
                jsApiList: ['closeWindow']
            })
        })
    }
}




