import TicketListItem from './TicketListItem';
    export default {
        data () {
            return {
                ticketList: [
                    // {
                    //     ID: '1423',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // },
                    // {
                    //     ID: '1421',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // },
                    // {
                    //     ID: '1422',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // },
                    // {
                    //     ID: '1419',
                    //     Subject: 'my computer cannot work',
                    //     Status: 'In Process',
                    //     Channel: 'WeChat'
                    // }
                ],
                loading: false,
                key: 0
            }
        },
        components: {
            TicketListItem: TicketListItem
        },
        methods: {
            openTicketDetail: function (id) {
                console.log(id);
                this.$router.push('/TicketDetail/' + id);
            },
            load: function () {
                this.loading = true;
                this.key += 10;
                this.$axios({
                    method: 'post',
                    url: this.CONFIG.url.getTicketList,
                    data: {
                        key: this.key
                    }
                }).then((res) => {
                    this.loading = false
                    if (res.data.length > 0) {
                        this.ticketList = this.ticketList.concat(res.data);
                    } else {
                        this.$toast.error('No more ticket!')
                    }
                })
            },
        },
        mounted() {
            let loading = this.$loading();
            this.$axios({
                method: 'post',
                url: this.CONFIG.url.getTicketList,
                data: {
                    key: this.key
                }
            }).then((res) => {
                loading.close();
                if (res.data.length == 0) {
                    this.$toast.error('No ticket found!');
                    return false;
                } else {
                    this.ticketList = this.ticketList.concat(res.data);
                }
            })
        },
    }



