export default {
    data () {
        return {
            ticketDetail:{
                ServiceRequestLocation: {}
            },
            isEnable: true,
            openDialog: false,
            msg: ''
        }
    },
    computed: {
        ticketID () {
            return this.$route.params.id;
        }
    },
    methods: {
        backToList: function () {
            this.$router.push('/TicketList');
        },
        open: function () {
            this.openDialog = true;
        },
        replyMsg: function () {
            let loading = this.$loading();
            if (this.msg.length > 0) {
                this.$axios({
                    method:'post',
                    url: this.CONFIG.url.replyMsg,
                    data: {
                        msg: this.msg,
                        ID: this.ticketDetail.SocialMediaActivityID
                    }
                }).then((res) => {
                    loading.close();
                    if(res.status == 200) {
                        this.openDialog = false;
                        this.$toast.success('Reply successfully!');
                    } else {
                        this.openDialog = false;
                        this.$toast.error('Reply Failed!')
                    }
                })
            }
            // this.openDialog = false;

        },
        cancel: function () {
            this.openDialog = false;
            this.msg = '';
        }
    },
    mounted () {
        // console.log(this.ticketID)
        let loading = this.$loading();
        this.$axios({
            method: 'post',
            url: this.CONFIG.url.getTicket,
            data: {
                ID: this.ticketID
            }
        }).then((res) => {
            loading.close();
            console.log(res.data);
            if (res.status == 200) {
                this.ticketDetail = res.data;
            }
        })
    }
}



